import {observable,autorun} from "mobx";

let todos = observable([
        { title: "Spoil tea" },
        { title: "Make coffee" }
    ]);

test = (data) => {
    todos.push(data)
}

class Stock{
    @observable data = ["dragonQuest","finalFantasy"];
    @observable counter = 0;
    @observable rest = [];

    increase() {
        this.counter = this.counter + 1;
    }
    decrease() {
        this.counter = this.counter - 1;
    }

    setdata(val) {
        console.log(val)
    }



}


let stock = new Stock;

export default stock;

